html {
    width: 100%;
    height: 100%;
    color: white;
}

.navbar-container {
    width: 85%;
    min-width: 50px;
    /* background-color: rgba(255, 0, 0, 0.083); */
    height: 50px;
    margin: 0 auto;
    padding-top: 50px;
    position: absolute;
    top: 0px;
    
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

/* #endregion misc*/

/* #region navbar */

.navbar a, h3{
    padding: 0px 10px;
    font-weight: 400;
    font-size: 17.5px;
    display: inline;
    transition: all 0.125s;
    transform: skewX(0.00001deg);
    text-decoration: none;
    color: white;
    transition: color 0.125s ease-in;
}

.navbar .left{
    float: left;
    position: relative;
    z-index: 1000000;
}

.navbar .right{
    float: right;
    position: relative;
    z-index: 1000000;
}

.navbar a:hover{
    cursor: pointer;
    color: rgba(255, 255, 255, 0.75);
    /* transform: skewX(-10deg); */
}

/* #endregion */

.title-container{
    /* width: 100%;
    height: 100%; */
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
}

.title {
    position: absolute;
    font-size: 150px;
    color: white;
    background: none;
    will-change: transform;
    overflow-x: visible;
    width: 75vw;
    transform: skewY(-10deg);
}

.parallax-core{
    position:absolute;
    width: 100%;
    height: 100vh;
    top: 0px;
    left: 0px;
    z-index: 1000;
    background: none;
    /* pointer-events: none; */
}




* {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    padding: 0;
    color: white;

}

html {
    width: 100%;
    height: 100%;
}

.infobox-container {
    height: 100vh;
    width: 85%;
    /* background-color: rgb(228, 123, 123); */
    position: relative;
    
}

.infobox-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}


.heading-container{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: -25%;
    transform: translate(0%, -50%);
    z-index: 100;
}

.heading {
    position: absolute;
    font-size: 125px;
    color: white;
    background: none;
    will-change: transform;
    overflow-x: visible;
    width: 50vw;
    will-change: transform;

}

.subheading {
    position: absolute;
    font-size: 40px;
    width: 500px;
    will-change: transform;
    left: 1000px;
    
}

.parallax-core-infobox {
    position:absolute;
    width: 100%;
    height: 100vh;
    /* top: 0px; */
    left: 0px;
    z-index: 1000;
    background: none;
    /* pointer-events: none; */
}

.circle-image{
    width: 250px;
    will-change: transform;
    height: 250px;
    border-radius: 50%;
    z-index: -10;
    position: absolute;
    top: 25%;
    /* left: -20%; */
    /* transform: translateY(-100%); */

}

