@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');

* {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    padding: 0;
    color: white;

}

html {
    width: 100%;
    height: 100%;
}

.infobox-container {
    height: 100vh;
    width: 85%;
    /* background-color: rgb(228, 123, 123); */
    position: relative;
    
}

.infobox-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}


.heading-container{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: -25%;
    transform: translate(0%, -50%);
    z-index: 100;
}

.heading {
    position: absolute;
    font-size: 125px;
    color: white;
    background: none;
    will-change: transform;
    overflow-x: visible;
    width: 50vw;
    will-change: transform;

}

.subheading {
    position: absolute;
    font-size: 40px;
    width: 500px;
    will-change: transform;
    left: 1000px;
    
}

.parallax-core-infobox {
    position:absolute;
    width: 100%;
    height: 100vh;
    /* top: 0px; */
    left: 0px;
    z-index: 1000;
    background: none;
    /* pointer-events: none; */
}

.circle-image{
    width: 250px;
    will-change: transform;
    height: 250px;
    border-radius: 50%;
    z-index: -10;
    position: absolute;
    top: 25%;
    /* left: -20%; */
    /* transform: translateY(-100%); */

}
